<template>
	<div  class="RFPick">
		<header><i class="iconfont icon-xiangzuo1" @click="toIndex"></i><span>拣货首页</span><i class="iconfont icon-saoma" @click="scan"></i></header>
		<div class="container">
			<div class="import">
				<span>任务组号</span>
				<input  placeholder="请输入内容"  @keyup.enter="ToRFPick_confirm"  @change="toUppercase('TaskGroupNum')"  @focus="g_focus('TaskGroupNum')"  ref="TaskGroupNum"  v-model="TaskGroupNum" ></input>
				<i v-if="TaskGroupNum" @click="clearInput('TaskGroupNum')" class="iconfont icon-cuowu"></i>
				<i class="iconfont icon-sousuo"  @click="toSelectOrder" ></i>
			</div>
			<span class="g_singleBtn" @click="ToRFPick_confirm">确定</span>
		</div>
		<div v-if="tipsHide"  @touchmove.prevent  class="g_tips">
			<div>
				<span>错误信息</span>
				<span>{{msg}}</span>
				<p  @click='tipsHideen(inputFocus)'>确定</p>
			</div>
		</div>
	</div>
</template>
<script type="text/javascript">
	export default{
		name:"RFPick",
		data(){
			return{
				TaskGroupNum:'',
				tipsHide:false,//提示框是否出现
				msg:"",//提示信息
				inputFocus:"",
			}
		},
		mounted() {

			// 聚焦
			this.$refs['TaskGroupNum'].focus();
			// 扫码
			window.QRScanAjax = this.QRScanAjax;
			// 控制字符长度
			$("input").attr("maxlength","50");
		}, 
		created(){

			var that = this;

			if(this.$route.query.TaskGroup){

				that.TaskGroupNum = that.$route.query.TaskGroup;
			}

		},
		methods:{
			// 接收iOS/android值
			QRScanAjax:function (qrcode){

				var inputName = this.$store.state.ScanQRCodeInputName;
				this[inputName] = qrcode;

				this.ToRFPick_confirm();
				
			},
			//返回上一页 首页
			toIndex:function(){
				this.$router.push({
					name:"Index",
				})
			},
			//跳转到拣货页面
			ToRFPick_confirm:function(){

				var that = this;

				if(!this.TaskGroupNum){
					
					that.tipsShow("TaskGroupNum");

					that.msg = "请填写任务组号";

					that.inputFocus = "TaskGroupNum";

					return;
				}

				var ReceiptUrl = this.HOST + "RFPickTask.do";

				var r_postData = {
					TaskGroup : this.TaskGroupNum,
				}

				this.httpRequest(ReceiptUrl,r_postData,function(res){

					if(res.data.data.length){
							
						that.$store.state.RFPick_SubmitData = res.data.data;

						that.$router.push({
							name:"RFPick_confirm",
							query:{
								
							}
						})
					}else{
						that.tipsShow("TaskGroupNum");

						that.msg = "没有数据";

						that.inputFocus = "TaskGroupNum";
					}
				},"TaskGroupNum")
			},
			// 跳转到 选择订单 页面
			toSelectOrder:function(){

				var that = this;

				var RFPickSelUrl = this.HOST + "RFPickSel.do";

				var postData = {
					"TaskGroup" : this.TaskGroupNum,
				}

				this.httpRequest(RFPickSelUrl,postData,function(res){

					if(res.data.data.length){
							
						that.$store.state.RFPick_SelectOrderData = res.data.data;

						that.$router.push({
							name:"RFPick_select",
							query:{
								TaskGroup:that.TaskGroupNum
							}
						})
					}else{
						that.tipsShow("TaskGroupNum");

						that.msg = "没有数据";

						that.inputFocus = "TaskGroupNum";
					}
				},"TaskGroupNum")
			}
		}
	}
</script>
<style scoped lang="less">
.RFPick{
	&>.container{
		input::placeholder{
			font-size: 25/50rem;
		}
		input{
			font-size: 25/50rem;
		}
		&>div.import{
			width:100%;
			height: 80/50rem;
			padding: 25/50rem 20/50rem 0;
			margin-bottom: 40/50rem;
			box-sizing: border-box;
			position: relative;
			span{
				width: 140/50rem;
			}
			input{
				margin-left: 10/50rem;
				width:490/50rem;	
				height:55/50rem;
				padding:0 10/50rem;
			}
			i{
				position:absolute;
				right:90/50rem;
				font-size: 30/50rem;
				top: 36/50rem;
			}
			i.icon-sousuo{
				right:30/50rem;
				font-size: 40/50rem;
				top: 30/50rem;
			}
		}
	}
}
@media screen and (max-width: 330px){
	.RFPick{
		&>.container{
			input::placeholder{
				font-size: 35/50rem;
			}
			input{
				font-size: 35/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:445/50rem;	
					height:65/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:120/50rem;
					font-size: 42/50rem;
					top: 26/50rem;
				}
				i.icon-sousuo{
					right:25/50rem;
					font-size:50/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
@media screen and (max-width: 277px){
	.RFPick{
		&>.container{
			input::placeholder{
				font-size: 40/50rem;
			}
			input{
				font-size: 40/50rem;
			}
			&>div.import{
				height: 100/50rem;
				line-height: 100/50rem;
				input{
					width:415/50rem;	
					height:70/50rem;
					margin: 0;
				}
				i{
					position:absolute;
					right:110/50rem;
					font-size: 48/50rem;
					top: 28.5/50rem;
				}
				i.icon-sousuo{
					right:20/50rem;
					font-size:56/50rem;
					top: 28/50rem;
				}
				
			}
		}
	}
}
</style>


